@import url("https://raw.githubusercontent.com/plone/plonetheme.barceloneta/master/plonetheme/barceloneta/theme/less/variables.plone.less");


@font-face {
  font-family: 'easynewsletter';
  src: url('./font/fontello.eot?54032556');
  src: url('./font/fontello.eot?54032556#iefix') format('embedded-opentype'),
       url('./font/fontello.woff?54032556') format('woff'),
       url('./font/fontello.ttf?54032556') format('truetype'),
       url('./font/fontello.svg?54032556#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
