// type icons
.icon-easynewsletter-actions::before {
  font-family: 'easynewsletter'!important;
  content: '\e800'!important;
}
body#visual-portal-wrapper.pat-plone .outer-wrapper {
  .contenttype-easynewsletter:before {
    font-family: 'easynewsletter'!important;
    content: '\e800'!important;
  }
  .contenttype-enlissue:before {
    font-family: 'easynewsletter'!important;
    content: '\e802'!important;
  }
  .contenttype-enlsubscriber:before {
    font-family: 'easynewsletter'!important;
    content: '\e803'!important;
  }
  .contenttype-enltemplate:before {
    font-family: 'easynewsletter'!important;
    content: '\e804'!important;
  }
}
// on right on toolbar
body#visual-portal-wrapper.pat-plone #edit-zone {
  .contenttype-easynewsletter:after {
    font-family: 'easynewsletter'!important;
    content: '\e800'!important;
  }
  .contenttype-enlissue:after {
    font-family: 'easynewsletter'!important;
    content: '\e802'!important;
  }
  .contenttype-enlsubscriber:after {
    font-family: 'easynewsletter'!important;
    content: '\e803'!important;
  }
  .contenttype-enltemplate:after {
    font-family: 'easynewsletter'!important;
    content: '\e804'!important;
  }
}

// states
.state-draft          {color: @plone-default-draft-color;
  &:hover, &:active, &:focus    {color: darken(@plone-default-draft-color,15%)}}
.state-master          {color: @plone-default-internal-color;
  &:hover, &:active, &:focus    {color: darken(@plone-default-internal-color,15%)}}
.state-sending          {color: @plone-default-pending-color;
  &:hover, &:active, &:focus    {color: darken(@plone-default-pending-color,15%)}}
.state-sent         {color: @plone-default-published-color;
  &:hover, &:active, &:focus    {color: darken(@plone-default-published-color,15%)}}


input.easynewsletter_text {
  width: 96%;
  margin-right: 0;
}

#enl_actions{
  margin: 1em 0 2em 0;
  padding:0.5em;
  border-bottom: 1px solid @plone-link-color;
}

#enl_actions li{
  display: inline;
  margin: 0 1em 0 0;
}

.portletNewsletterSubscriber div.field span.required::after {
  position: absolute;
}

.template-atct_edit.portaltype-enltemplate #archetypes-fieldname-body textarea{
  height: 850px;
  min-width: 800px;
  font-family: Courier New;
  font-size: 90%;
}

.template-enl_subscribers_view .search {
  margin-top: 1.5em;
  margin-bottom: 1.5em;
  legend {
    margin-bottom: 0;
    border-bottom: 0;
    font-size: @plone-font-size-h3;
    font-weight: @plone-font-weight-regular;
  }
  input[type="text"] {
    width: unset;
    display: inline-block;
  }
}
